.account-box{
  display: flex;
  background-color: #F76c6c;
  height: 10%;
  padding: 2%;
  width: 80%;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
}


.accountlinks{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.accountlinks a{
  color: black;
}

#myTools{
  display: flex;
  flex-direction: column;
  align-items: center;
  max-height: 50%;
  min-height: 20%;
  width: 100%;
  text-align: center;
}

#myTools a{
  padding: 5%;
}

.bottomnav-wrapper{
  background-color: #374785;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  bottom: 0;
  position: fixed;
  height: 5vh;
}

.bottomnav-wrapper a{
  color: white;
}
/* hide on anything that isn't */
@media (min-width: 767px) {
  .bottomnav-wrapper{
      display:none;
  }
}


.homepage-body {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  font-family: "IBM Plex Serif", serif;
}

.task-wrapper{
  background-color: white;
  width: 90%;
  height: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.tool-title{
  font-family: 'Montserrat', sans-serif;
}

.singletask{
width: 100%;
padding-top: 2%;
padding-bottom: 2%;
max-height: 5%;
text-align: center;
font-family: 'Montserrat', sans-serif;
font-size: 1.5em;
}


.singletask:hover{
 background-color: wheat;
}


#task-box{
background-color: #F8E9A1;
width: 90%;
height: 90%;
display:flex;
justify-content: center;
align-items: center;
flex-direction: column;
}

.app-wrap{
  display: flex;
  max-height: 100vh;
  height: 100vh;
}

#sitebody{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

